import request from '@/plugin/axios';
export default {
  name: 'task-list',
  data: function data() {
    return {
      filename: __filename,
      loading: true,
      query: {
        title: '',
        publish: null
      },
      create: {
        id: '',
        title: '',
        content: '',
        publish: false,
        attachment: ''
      },
      pagination: {
        currentPage: 1,
        pageSize: 20,
        pageSizes: [10, 20, 30, 50, 100],
        total: 0
      },
      tableData: [],
      formLabelWidth: '60px',
      dialogFormVisible: false,
      options: [{
        value: true,
        label: '已发布'
      }, {
        value: false,
        label: '未发布'
      }]
    };
  },
  created: function created() {
    this.getTableData();
  },
  watch: {
    'pagination.currentPage': function paginationCurrentPage(val) {
      this.getTableData();
    },
    'pagination.pageSize': function paginationPageSize(val) {
      this.getTableData();
    }
  },
  methods: {
    formatterFinish: function formatterFinish(row, column, cellValue) {
      if (row.publish === true) {
        return '已发布';
      } else {
        return '未发布';
      }
    },
    handleAvatarSuccess: function handleAvatarSuccess(res, file) {
      if (res.data) {
        this.create.attachment = res.data.url;
      }
    },
    handleExceed: function handleExceed(files, fileList) {
      this.$message.warning("\u5F53\u524D\u9650\u5236\u9009\u62E9 1 \u4E2A\u6587\u4EF6\uFF0C\u672C\u6B21\u9009\u62E9\u4E86 ".concat(files.length, " \u4E2A\u6587\u4EF6\uFF0C\u5171\u9009\u62E9\u4E86 ").concat(files.length + fileList.length, " \u4E2A\u6587\u4EF6"));
    },
    beforeAvatarUpload: function beforeAvatarUpload(file) {
      var isLt2M = file.size / 1024 / 1024 < 20;

      if (!isLt2M) {
        this.$message.error('上传文件大小不能超过 20MB!');
      }

      return isLt2M;
    },
    publish: function publish(row) {
      var _this = this;

      var params = {
        id: row.id
      };
      request.post('/bulletin/publish', params).then(function (res) {
        if (res.code === 0) {
          _this.$message.success('任务完成');

          _this.getTableData();
        }
      });
    },
    search: function search() {
      this.pagination.currentPage = 1;
      this.getTableData();
    },
    handleEdit: function handleEdit(row) {
      var _this2 = this;

      if (row.id) {
        this.create.id = row.id;
        request.get("/bulletin/".concat(row.id)).then(function (res) {
          _this2.create = res.data;
        });
        this.dialogFormVisible = true;
      }
    },
    addBulletin: function addBulletin() {
      // 清空数据
      this.create.id = '';
      this.dialogFormVisible = true;
      this.$refs['create'].resetFields();
    },
    reset: function reset(formName) {
      this.$refs[formName].resetFields();
      this.getTableData();
    },
    handleSizeChange: function handleSizeChange(val) {
      this.pagination.pageSize = val;
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.pagination.currentPage = val;
    },
    getTableData: function getTableData() {
      var _this3 = this;

      this.loading = true;
      var page = this.pagination;
      var params = {
        'index': page.currentPage,
        'size': page.pageSize
      };
      var filters = {
        'title': this.query.title,
        'publish': this.query.publish
      };
      params.filters = filters;
      request.get('/bulletin', {
        params: params
      }).then(function (res) {
        _this3.pagination.total = res.data.count;

        if ((_this3.pagination.currentPage - 1) * _this3.pagination.pageSize >= _this3.pagination.total) {
          _this3.pagination.currentPage--;
          return;
        }

        _this3.tableData = res.data.list;
        _this3.loading = false;
      }).catch(function () {
        _this3.loading = false;
      });
    },
    save: function save() {
      var _this4 = this;

      var params = JSON.parse(JSON.stringify(this.create));
      request.post('/bulletin', params).then(function (res) {
        if (res.code === 0) {
          _this4.dialogFormVisible = false;

          _this4.$refs['create'].resetFields();

          _this4.getTableData();
        }
      });
    },
    handleDelete: function handleDelete(row) {
      var _this5 = this;

      request.delete("/bulletin/".concat(row.id)).then(function (res) {
        if (res.code === 0) {
          _this5.getTableData();

          _this5.$message({
            message: '删除成功',
            type: 'success'
          });
        }
      });
    }
  }
};